import React, {FC} from 'react';
import {Card, CardContent, CardMedia, Typography} from "@mui/material";
import {CartProduct} from "../../models/CartProduct";
import {NavLink} from "react-router-dom";
import {AddBox} from "@mui/icons-material";
import IndeterminateCheckBoxIcon from '@mui/icons-material/IndeterminateCheckBox';
import {useModifyCountMutation} from "../../services/cartService";

const CartSingleItem: FC<CartProduct> = ({name, image, price, color, size, count, id, createdAt}) => {
    const [modifyCount, {isError,  isLoading}] = useModifyCountMutation()
    const handleIncrease = () => modifyCount(
        {name, image, price, color, size, count: ++count, id, createdAt}
    )
    return (
        <Card sx={{display: 'flex', boxShadow: 0, backgroundColor: 'white', mb: 2}}>
            <NavLink
                to={`/productName`}
                className="cart-img__wrapper"
            >
                <CardMedia
                    component='img'
                    image={image}
                    alt='product card'
                />
            </NavLink>
            <CardContent sx={{
                width: '100%',
                p: '0 0 10px 20px'
            }}>
                <Typography fontSize={13}>
                    <span className='flex'>
                        <NavLink to={`/productName`}>{name}</NavLink>
                        <span>${price}</span>
                    </span>
                    <span className='cart-features'>
                        Size: <span className="bold">{size}</span>
                    </span>
                    <span className='cart-features'>
                        Color: <span className="bold">{color}</span>
                    </span>
                    <span className="cart-features">
                        <IndeterminateCheckBoxIcon
                            fontSize='small'
                        />
                        <span className="cart-count">{count}</span>
                        <AddBox
                            onClick={handleIncrease}
                            fontSize='small'
                        />
                    </span>
                </Typography>
            </CardContent>
        </Card>
    );
};

export default CartSingleItem;